<template>
  <div class="b-table-container">
    <table
      :class="[
        'b-table',
         autoWidth && 'b-table-auto'
      ]">
      <thead class="b-table__header">
        <tr class="b-table__row b-table__row--header">
          <th
            v-if="selectable"
            class="b-table__column-selection"
            @mouseenter="isHoveredSelectionColumn = true"
            @mouseleave="isHoveredSelectionColumn = false"
          >
            <Checkbox
              v-show="items.length > 0"
              :model-value="selectAll || isIndeterminate"
              @click.prevent="selectAll = isIndeterminate ? true : !selectAll"
              :indeterminate="isIndeterminate"
              :disabled="isAnyRowLoading"
            />
          </th>
          <slot v-for="header in headers" v-bind="{ header }" name="header">
            <th :key="header.key">
              {{ header.column_name }}
            </th>
          </slot>
          <th v-if="$slots.action || moreActionSlots?.length"></th>
        </tr>
      </thead>
      <tbody class="b-table__body">
        <tr
          v-for="(item, itemIndex) of items"
          v-intersect="() => handleIntersectionObserver(item)"
          @click="handleClickRow(item)"
          :class="[
            'b-table__row b-table__row--body relative',
            clickableRow && 'b-table__row--clickable',
            'group',
            item.loading && 'is-loading'
          ]"
          :key="`${item[itemValue]}-${itemIndex}-${dataTimestamp}`"
        >
          <td
            v-if="selectable"
            @mouseenter="isHoveredSelectionColumn = true"
            @mouseleave="isHoveredSelectionColumn = false"
          >
            <Checkbox
              :model-value="selectedValues.includes(item[itemValue || ''])"
              :disabled="isAnyRowLoading"
              @click.prevent="handleSelectionChange(item)"
            />
          </td>
          <slot
            v-for="header in headers"
            :key="header.key"
            v-bind="{
              row: item,
              column: item[header.key],
              key: header.key,
              rowIndex: itemIndex,
              preventRowClick,
              resetRowClick
            }"
            name="column"
          >
            <td>
              {{ item[header.key] }}
            </td>
          </slot>
          <td
            v-if="$slots.action && !moreActionSlots?.length"
            class="group-hover:opacity-100 transition duration-300 opacity-0"
            @mouseenter="isHoveredActionColumn = true"
            @mouseleave="isHoveredActionColumn = false"
          >
            <slot
              v-bind="{ row: item, rowIndex: itemIndex }"
              name="action"
            />
          </td>
          <td
            v-else-if="moreActionSlots.length"
            class="transition duration-300 opacity-0 group-hover:opacity-100"
            :class="{ 'opacity-100': isMoreActionDropdownOpen && isMoreActionDropdownOpenKey === itemIndex }"
            @mouseenter="isHoveredActionColumn = true"
            @mouseleave="isHoveredActionColumn = false">
            <div>
              <Dropdown
                trigger="hover"
                placement="bottom-end"
                @visible-change="(visible) => {
                  isMoreActionDropdownOpen = visible
                  isMoreActionDropdownOpenKey = itemIndex
                }">
                <Button
                  color="grey"
                  view="outlined"
                  class="text-base"
                  dense>
                  <span class="material-icons">
                    more_vert
                  </span>
                </Button>
                <template #dropdown>
                  <DropdownMenu
                    class="p-2 rounded-lg">
                    <DropdownItem
                      v-for="(moreAction, n) in moreActionSlots"
                      :key="n"
                      class="p-0 leading-none rounded-lg">
                      <div class="text-teal-04 hover:text-teal-03">
                        <slot v-bind="{ row: item, rowIndex: itemIndex }" :name="moreAction" />
                      </div>
                    </DropdownItem>
                  </DropdownMenu>
                </template>
              </Dropdown>
            </div>
          </td>
        </tr>
      </tbody>
      <tfoot class="b-table__footer">
        <tr class="b-table__row b-table__row--footer">
          <slot name="footer" v-bind="{ headers }"></slot>
        </tr>
        <!-- Multiple footer rows -->
        <slot v-if="$slots.footerRows" name="footerRows" v-bind="{ headers }"></slot>
      </tfoot>
    </table>
  </div>
</template>

<script>
import { computed, defineComponent, ref, watch } from 'vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'BTable',
  components: {
    Checkbox,
    Button
  },
  props: {
    headers: {
      type: Array,
      default: () => []
    },
    items: {
      type: Array,
      default: () => []
    },
    itemValue: {
      type: String,
      default: 'id'
    },
    loading: {
      type: Boolean,
      default: false
    },
    selected: {
      type: Array,
      default: () => []
    },
    clickableRow: {
      type: Boolean,
      default: false
    },
    selectable: {
      type: Boolean,
      default: false
    },
    minWidth: {
      type: String,
      default: '200px'
    },
    intersect: {
      type: Boolean,
      default: false
    },
    observerTable: {
      type: Function,
      default: undefined
      // Lazy Load for each row if row need fetch another data
    },
    moreActions: {
      type: Array,
      default: undefined
      // example you just send ['delete',  'edit', 'preview']
      // this props will generate new slots with camelCase named, using prefix ex: 'actionDelete'
      // new ui standard all button action inside dropdown
    },
    prefixMoreAction: {
      type: String,
      default: 'action'
    },
    timeStamp: {
      type: Boolean,
      default: true
    },
    autoWidth: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:selected', 'rowClick', 'update:singleSelected'],
  setup(props, { emit }) {
    const isHoveredSelectionColumn = ref(false)
    const isHoveredActionColumn = ref(false)
    const isPreventRowClick = ref(false)
    const isMoreActionDropdownOpen = ref(false)
    const isMoreActionDropdownOpenKey = ref(null)
    const dataTimestamp = ref(new Date(Date.now()).getTime())
    const selected = computed({
      set: (selected) => emit('update:selected', selected),
      get: () => props.selected
    })
    const selectedValues = computed(() =>
      selected.value.map((item) => item[props.itemValue])
    )
    const selectedItemFilteredByItemsProp = computed(() => {
      return props.items
        .map((item) => item[props.itemValue])
        .filter((item) => selectedValues.value.includes(item))
    })

    const moreActionSlots = computed(() => {
      return props.moreActions ? props.moreActions.map((str) => {
        return prefixForActionSlots(str, props.prefixMoreAction)
      }) : []
    })
    const isAnyRowLoading = computed(() =>
      props.items.some((item) => item.loading === true)
    )
    const isIndeterminate = computed(() => {
      const filteredSelectedItems = selectedItemFilteredByItemsProp.value
      const hasSelectedItems = filteredSelectedItems.length > 0
      const notAllItemsSelected =
        filteredSelectedItems.length < props.items.length
      return hasSelectedItems && notAllItemsSelected
    })

    const selectAll = computed({
      set: (value) => {
        if (value) {
          const newItems = props.items.filter((item) => {
            const currentItem = item[props.itemValue]
            return !selectedValues.value.includes(currentItem)
          })
          selected.value = [...selected.value, ...newItems]
        } else {
          const itemIds = props.items.map((item) => item[props.itemValue])
          selected.value = selected.value.filter(
            (item) => !itemIds.includes(item[props.itemValue])
          )
        }
      },
      get: () => selected.value.length >= props.items.length
    })
    const minWidth = computed(() => props.minWidth)
    const handleClickRow = (row) => {
      if (
        !props.clickableRow ||
        isHoveredSelectionColumn.value ||
        isHoveredActionColumn.value ||
        isPreventRowClick.value
      )
        return
      emit('rowClick', row)
    }
    const handleSelectionChange = (item) => {
      const isCurrentItemSelected = selectedValues.value.includes(
        item[props.itemValue]
      )
      if (isCurrentItemSelected) {
        selected.value = selected.value.filter(
          (selectedItem) =>
            selectedItem[props.itemValue] !== item[props.itemValue]
        )
        return
      }
      selected.value = [...selected.value, item]
      emit('update:singleSelected', item)
    }
    const preventRowClick = () => {
      isPreventRowClick.value = true
    }
    const resetRowClick = () => {
      isPreventRowClick.value = false
    }
    const handleIntersectionObserver = (e) => {
      if (props.observerTable && typeof props.observerTable === 'function' && props.intersect) {
        props.observerTable(e)
      }
    }
    const prefixForActionSlots = (str, prefix = 'action') => {
      return (
        prefix +
        str
          .split(/[\s-]+/)
          .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
          .join('')
      )
    }

    watch(() => props.items, () => {
      if (props.timeStamp) dataTimestamp.value = new Date(Date.now()).getTime()
    })

    return {
      selected,
      selectedValues,
      isHoveredSelectionColumn,
      isHoveredActionColumn,
      isIndeterminate,
      isPreventRowClick,
      isMoreActionDropdownOpenKey,
      isMoreActionDropdownOpen,
      isAnyRowLoading,
      selectAll,
      moreActionSlots,
      dataTimestamp,
      handleClickRow,
      handleSelectionChange,
      preventRowClick,
      resetRowClick,
      handleIntersectionObserver
    }
  }
})
</script>

<style lang="scss">
.b-table {
  @apply w-full;
  &.b-table-auto {
    @apply w-full table-auto;
    th {
      min-width: auto !important;

      &.b-table__column-selection {
        min-width: auto !important;
        width: auto !important;
      }
    }
  }
}

.b-table-container {
  @apply relative w-full overflow-auto border-b border-grey-01;
}

.b-table__row {
  @apply border-b border-grey-01;
  &:hover {
    background-color: rgba(0, 0, 0, 0.03);
  }

  th,
  td {
    @apply p-5 text-left;
  }

  th {
    min-width: v-bind(minWidth);

    &.b-table__column-selection {
      min-width: 60px !important;
      width: 60px !important;
    }
  }

  &--clickable {
    @apply cursor-pointer;
  }
  &.is-loading {
    position: relative;
    &:hover {
      background-color: transparent !important;
    }
    td {
      position: relative;
      cursor: wait;
      &::after {
        content: '';
        position: absolute;
        inset: 0;
        background: rgba(255, 255, 255, 0.5);
        animation: flicker-bg 1s ease-in-out infinite alternate;
        z-index: 10;
        pointer-events: all;
      }
    }
  }
  // &.is-loading::after {
  //   @apply border-teal-04;
  //   content: '';
  //   position: absolute;
  //   top: 50%;
  //   left: 50%;
  //   width: 20px;
  //   height: 20px;
  //   margin: -10px 0 0 -10px;
  //   border: 2px solid;
  //   border-top-color: transparent;
  //   border-radius: 50%;
  //   animation: spin 0.8s linear infinite;
  //   z-index: 10;
  //   pointer-events: all;
  // }
}

.b-table__header,
.b-table__footer {
  @apply sticky bg-grey-01;
}

.b-table__header {
  inset-block-start: 0;
}

.b-table__footer {
  inset-block-end: 0;
}
@keyframes flicker-bg {
  0% {
    background-color: rgba(255, 255, 255, 0.6);
  }
  100% {
    background-color: rgba(255, 255, 255, 0.3);
  }
}
</style>
